@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
:root{--primary-color: rgb(91, 164, 164);--background-color: hsl(180, 52%, 96%);--filter-table: hsl(180, 31%, 95%);--dark-greyish: hsl(180, 8%, 52%);--very-dark-greyish: hsl(180, 14%, 20%)}*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{font-size:15px;font-weight:500;box-sizing:border-box}body{font-family:"Spartan", sans-serif;background-color:hsl(180, 52%, 96%);background-color:var(--background-color)}@media only screen and (max-width: 375px){html{font-size:0.75rem}}.header{width:100%;height:10rem;background-image:url(/static/media/bg-header-desktop.8694f466.svg);background-color:rgb(91, 164, 164);background-color:var(--primary-color)}.header-container{display:flex;flex-direction:column;align-items:center;margin-top:calc(2vw + -4rem)}.header-container ul{width:70vw;background-color:#fff;display:flex;align-items:center;padding:1.5rem 2rem;border-radius:5px;margin:1.3rem 0;position:relative;box-shadow:0px 4px 6px 4px rgba(91,164,164,0.2);margin-bottom:-2rem;position:relative}@media only screen and (max-width: 375px){.header-container ul{flex-wrap:wrap;margin-bottom:1rem}}.header-container ul li{list-style:none;background-color:hsl(180, 52%, 96%);background-color:var(--background-color);color:rgb(91, 164, 164);color:var(--primary-color);font-weight:700;padding:0 0 0 0.5rem;display:flex;align-items:center;border-radius:5px}@media only screen and (max-width: 375px){.header-container ul li{margin-bottom:0.5rem}}.header-container ul li:not(:last-child){margin-right:1rem}.header-container ul li .close{background-color:rgb(91, 164, 164);background-color:var(--primary-color);border:none;border-radius:0 5px 5px 0;cursor:pointer;margin-left:0.5rem;transition:all 0.2s}.header-container ul li .close img{padding:0.5rem}.header-container ul li .close:hover{background-color:hsl(180, 14%, 20%);background-color:var(--very-dark-greyish)}.header-container ul a{color:hsl(180, 14%, 20%);color:var(--very-dark-greyish);position:absolute;right:1.5rem;text-decoration:none}.header-container ul a:hover{color:rgb(91, 164, 164);color:var(--primary-color);text-decoration:underline}.job-container{width:70vw;background-color:#fff;display:flex;align-items:center;padding:1.5rem 2rem;border-radius:5px;margin:1.3rem 0;position:relative;box-shadow:0px 4px 6px 4px rgba(91,164,164,0.2)}@media only screen and (max-width: 375px){.job-container{flex-direction:column;align-items:flex-start;width:80vw;padding-bottom:1rem;margin-bottom:2rem}}.job-container .logo{display:flex;align-items:center;padding-right:1.5rem}@media only screen and (max-width: 375px){.job-container .logo{position:absolute;top:-2rem}.job-container .logo img{width:4rem;height:auto}}.job-container .part1{display:flex;flex-direction:column;flex-grow:1}@media only screen and (max-width: 375px){.job-container .part1{flex-grow:0;padding:1.3rem 0}}.job-container .part1 .company .cname{font-weight:700;color:rgb(91, 164, 164);color:var(--primary-color);margin-right:0.8rem}.job-container .part1 .company .new,.job-container .part1 .company .featured{color:#fff;background-color:rgb(91, 164, 164);background-color:var(--primary-color);border-radius:1rem;padding:0.4rem;font-size:0.7rem;font-weight:700;text-transform:uppercase;margin-right:0.8rem}.job-container .part1 .company .featured{background-color:hsl(180, 14%, 20%);background-color:var(--very-dark-greyish)}.job-container .part1 .position{font-weight:700;font-size:1.1rem;display:inline-block;padding:0.7rem 0;cursor:pointer}.job-container .part1 .position:hover{color:rgb(91, 164, 164);color:var(--primary-color)}.job-container .part1 .details{color:hsl(180, 8%, 52%);color:var(--dark-greyish)}.job-container .part2{display:flex;flex-wrap:wrap;justify-content:space-between}@media only screen and (max-width: 375px){.job-container .part2{border-top:1px solid hsl(180, 8%, 52%);border-top:1px solid var(--dark-greyish);padding-top:1rem;justify-content:flex-start}}.job-container .part2 span{background-color:hsl(180, 52%, 96%);background-color:var(--background-color);color:rgb(91, 164, 164);color:var(--primary-color);font-weight:700;padding:0.5rem;border-radius:5px;cursor:pointer}.job-container .part2 span:not(:last-child){margin-right:1rem}.job-container .part2 span:hover{background-color:rgb(91, 164, 164);background-color:var(--primary-color);color:#fff}@media only screen and (max-width: 375px){.job-container .part2 span{margin-bottom:1rem}}.job-container--borderLeft{border-left:5px solid rgb(91, 164, 164);border-left:5px solid var(--primary-color)}.jobs{display:flex;flex-direction:column;align-items:center;margin-top:calc(2vw + 2rem)}

